import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AllprofessorsComponent } from './all-professors/all-professors.component';
import { AddProfessorComponent } from './add-professor/add-professor.component';
import { EditProfessorComponent } from './edit-professor/edit-professor.component';
import { AboutProfessorComponent } from './about-professor/about-professor.component';
import { VisitorsBookComponent } from './visitors-book/visitors-book.component';
import { AllVisitorsBookComponent } from './all-visitors-book/all-visitors-book.component';
import { EditVisitorBookComponent } from './edit-visitor-book/edit-visitor-book.component';
import { AddVisitorBookComponent } from './add-visitor-book/add-visitor-book.component';
import { AboutVisitorBookComponent } from './about-visitor-book/about-visitor-book.component';

import { AllCorrespondenceComponent } from './all-correspondence/all-correspondence.component';
import { AboutCorrespondenceComponent } from './about-correspondence/about-correspondence.component';
import { EditCorrespondenceComponent } from './edit-correspondence/edit-correspondence.component';
import { AddCorrespondenceComponent } from './add-correspondence/add-correspondence.component';

import { AllComplaintComponent } from './all-complaint/all-complaint.component';
import { AboutComplaintComponent } from './about-complaint/about-complaint.component';
import { EditComplaintComponent } from './edit-complaint/edit-complaint.component';
import { AddComplaintComponent } from './add-complaint/add-complaint.component';

import { AddAdmissionEnquiryComponent } from './add-admission-enquiry/add-admission-enquiry.component';
import { AllAdmissionEnquiryComponent } from './all-admission-enquiry/all-admission-enquiry.component';
import { AboutAdmissionEnquiryComponent } from './about-admission-enquiry/about-admission-enquiry.component';
import { EditAdmissionEnquiryComponent } from './edit-admission-enquiry/edit-admission-enquiry.component';

import { AllPhoneLogsComponent } from './all-phone-logs/all-phone-logs.component';
