import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Component, Inject, OnInit } from '@angular/core';
// import { ProfessorsService, PhoneCallLog } from '../../../all-professors/professors.service';
// import { ProfessorsService, PhoneCallLog } from '../../professors.service';
import { CorrespondenceService } from '../../correspondence.service';
import { Correspondence } from '../../correspondence';
import {
  FormControl,
  Validators,
  FormGroup,
  FormBuilder
} from '@angular/forms';
// import { Professors } from '../../../all-professors/professors.model';
import { formatDate } from '@angular/common';

@Component({
  selector: 'app-correspondence-form-dialog',
  templateUrl: './correspondence-form-dialog.component.html',
  styleUrls: ['./correspondence-form-dialog.component.sass']
})
export class CorrespondenceFormDialogComponent implements OnInit {

  action: string;
  dialogTitle: string;
  proForm: FormGroup;
  correspondence: Correspondence;

  constructor(
    public dialogRef: MatDialogRef<CorrespondenceFormDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public correspondenceService: CorrespondenceService,
    private fb: FormBuilder
  ) {
    // Set the defaults
    this.action = data.action;
    if (this.action === 'edit') {
      this.dialogTitle = data.correspondence.name;
      this.correspondence = data.correspondence;
    } else {
      this.dialogTitle = 'New Phone Call Log';
      this.correspondence = new Correspondence({});
    }
    this.proForm = this.createContactForm();
  }
  formControl = new FormControl('', [
    Validators.required
    // Validators.email,
  ]);
  getErrorMessage() {
    return this.formControl.hasError('required')
      ? 'Required field'
      : this.formControl.hasError('email')
      ? 'Not a valid email'
      : '';
  }
  createContactForm(): FormGroup {
    return this.fb.group({
      id: [this.correspondence.id],
      attach_document: [this.correspondence.attach_document],
      from_title: [this.correspondence.from_title],
      to_title: [this.correspondence.to_title],
      reference_no: [this.correspondence.reference_no],
      address: [this.correspondence.address],
      // date: [ this.correspondence.date,
      //   [Validators.required]
      // ],
      date: [ formatDate(this.correspondence.date, 'yyyy-MM-dd', 'en'),
        [Validators.required]
      ],
      call_type: [this.correspondence.call_type],
      note: [this.correspondence.note],
    });
  }
  onChange(event): void {
    if (event.target.files && event.target.files.length > 0) {
      this.correspondence.attach_document = event.target.files[0];
    }
  }
  submit() {
    // emppty stuff
    
  }
  
  onNoClick(): void {
    this.dialogRef.close();
  }
  public confirmAdd(): void {
    this.correspondenceService.addCorrespondence(this.proForm.getRawValue());
    this.correspondenceService.createCorrespondence(
      this.proForm.value,

    ).subscribe((res) => {
      
      console.log(res);
      console.log("successfully logged in");
      
    }, (error) => {
      console.error(error);
    });

   }

  ngOnInit(): void {
  }

}
