import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { SetupService, SetUp } from './setup.service';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
@Component({
  selector: 'app-setup',
  templateUrl: './setup.component.html',
  styleUrls: ['./setup.component.sass']
})
export class SetupComponent implements OnInit {

  // domain: any;
  // loginForm: FormGroup;
  setup: SetUp = new SetUp();
  submitted = false;
  returnUrl: string;
  hide = true;
  success: any;
  error: any;
  constructor(
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private router: Router,
    private setupService: SetupService,
    private snackBar: MatSnackBar
  ) {
    // route.params.subscribe(params => { this.domain = params['id']; });
  }
  ngOnInit() {
    this.getSetUp();
    this.setup.about_us = "We are here today";
    // this.loginForm = this.formBuilder.group({
    //   user_subdomain: [''],
    //   username: [''],
    //   password: [''],
    //   role: [''],
    // });
    // // get return url from route parameters or default to '/'
    // this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/';
  }
  // get f() {
  //   return this.loginForm.controls;
  // }
  // about_us: string,
  // logo: any,
  // contact_us: string,
  // header_content: string,
  // footer_content: string,
  // color: string,
  showNotification(colorName, text, placementFrom, placementAlign) {
    this.snackBar.open(text, '', {
      duration: 2000,
      verticalPosition: placementFrom,
      horizontalPosition: placementAlign,
      panelClass: colorName,
    });
  }

  editSetup(): void {
    this.setupService.editSetup(
      this.setup.id,
      this.setup.template,
      this.setup.about_us,
      this.setup.logo,
      this.setup.contact_us,
      this.setup.header_content,
      this.setup.footer_content,
      this.setup.color
    ).subscribe((res) => {
      // console.log(this.login.role);
      console.log(res);
      // console.log(res.data.id);
      console.log("successfully logged in");
      this.showNotification(
        'snackbar-success',
        'Set Up successfully updated...!!!',
        'bottom',
        'center'
      );
      this.getSetUp();
      this.success = res;
      // this.success = "Your super admin account has been successfully created"
      
    }, (error) => {
      console.error(error);
      
    });

  }

  onChange(event): void {
    if (event.target.files && event.target.files.length > 0) {
      this.setup.logo = event.target.files[0];
    }
  }

  getSetUp() {
    
    this.setupService.getSetup()
     .subscribe(
       (response) => {
         this.setup = response[0];
         console.log(response[0]);
        //  this.getParentProfile();

       }, (error) => {
         console.error(error);
       }
     );

  }


}
