import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Component, Inject, OnInit } from '@angular/core';
// import { ProfessorsService, PhoneCallLog } from '../../../all-professors/professors.service';
import { EnquiryService } from '../../enquiry.service';
// import { ProfessorsService, PhoneCallLog } from '../../professors.service';
import { Enquiry } from '../../enquiry';
import {
  FormControl,
  Validators,
  FormGroup,
  FormBuilder
} from '@angular/forms';
// import { Professors } from '../../../all-professors/professors.model';
import { formatDate } from '@angular/common';

@Component({
  selector: 'app-enquiry-edit',
  templateUrl: './enquiry-edit.component.html',
  styleUrls: ['./enquiry-edit.component.sass']
})
export class EnquiryEditComponent implements OnInit {

  action: string;
  dialogTitle: string;
  proForm: FormGroup;
  enquiry: Enquiry;

  constructor(
    public dialogRef: MatDialogRef<EnquiryEditComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public enquiryService: EnquiryService,
    private fb: FormBuilder
  ) {
    // Set the defaults
    this.action = data.action;
    if (this.action === 'edit') {
      this.dialogTitle = 'first';
      this.dialogTitle = data.enquiry.name;
      this.enquiry = data.enquiry;
    } else {
      this.dialogTitle = 'New Phone Call Log';
      this.enquiry = new Enquiry({});
    }
    this.proForm = this.createContactForm();
  }
  formControl = new FormControl('', [
    Validators.required
    // Validators.email,
  ]);
  getErrorMessage() {
    return this.formControl.hasError('required')
      ? 'Required field'
      : this.formControl.hasError('email')
      ? 'Not a valid email'
      : '';
  }
  createContactForm(): FormGroup {
    return this.fb.group({
    
      id: [this.enquiry.id],
      name: [this.enquiry.name],
      address: [this.enquiry.address],
      email: [
        this.enquiry.email,
        [Validators.required, Validators.email, Validators.minLength(5)]
      ],
      // follow_up_date: [this.enquiry.follow_up_date,
      //   [Validators.required]
      // ],
      follow_up_date: [ formatDate(this.enquiry.follow_up_date, 'yyyy-MM-dd', 'en'),
        [Validators.required]
      ],
      no_child_to_enroll: [this.enquiry.no_child_to_enroll],
      phone_number: [this.enquiry.phone_number],
      description: [this.enquiry.description],
      reference: [this.enquiry.reference],
      status: [this.enquiry.status],
      note: [this.enquiry.note],
      admin_note: [this.enquiry.admin_note],
    });
  }
  submit() {
    // emppty stuff
    
  }
  
  onNoClick(): void {
    this.dialogRef.close();
  }
  public confirmEdit(): void {
    this.enquiryService.updateAdmissionEnquiry(this.proForm.getRawValue());
    this.enquiryService.editAdmissionEnquiry(
      // user_subdomain,
      this.proForm.value,
      // this..name,
      // this.professors.call_duration,
      // this.professors.description,
      // this.professors.follow_up_date,
      // this.professors.note,
      // this.professors.call_type

    ).subscribe((res) => {
      // this.router.navigateByUrl('/create-client/' + `${res.data.id}`);
      console.log(res);
      // console.log(res.data.id);
      console.log("successfully logged in");
      // this.success = res;
      // this.success = "Your super admin account has been successfully created"
      
    }, (error) => {
      console.error(error);
      // this.error = error.error;
    });

   }

  // ngOnInit(): void {
  // }

  ngOnInit(): void {
  }


}
