import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Component, Inject } from '@angular/core';
// import { ProfessorsService } from '../../../all-professors/professors.service';
import { CorrespondenceService } from '../../correspondence.service';
import { Correspondence } from '../../correspondence';

@Component({
  selector: 'app-correspondence-delete',
  templateUrl: './correspondence-delete.component.html',
  styleUrls: ['./correspondence-delete.component.sass']
})
export class CorrespondenceDeleteComponent {

  constructor(
    public dialogRef: MatDialogRef<CorrespondenceDeleteComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public correspondenceService: CorrespondenceService
  ) {}
  onNoClick(): void {
    this.dialogRef.close();
  }
  confirmDelete(): void {
    this.correspondenceService.deleteCorrespondence(this.data.id);
  }

}