import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Component, Inject } from '@angular/core';
import { ProfessorsService } from '../../../all-professors/professors.service';
import { PhoneLogsService } from '../../phone-logs.service';
import { PhoneLogs } from '../../phone-logs';

@Component({
  selector: 'app-phone-logs-delete',
  templateUrl: './phone-logs-delete.component.html',
  styleUrls: ['./phone-logs-delete.component.sass']
})
export class PhoneLogsDeleteComponent {

  constructor(
    public dialogRef: MatDialogRef<PhoneLogsDeleteComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public phoneLogsService: PhoneLogsService
  ) {}
  onNoClick(): void {
    this.dialogRef.close();
  }
  confirmDelete(): void {
    this.phoneLogsService.deletePhoneLog(this.data.id);
  }

}
