import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Complaint } from './complaint';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable } from 'rxjs';
import { finalize, tap } from 'rxjs/operators';
import { environment } from '../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ComplaintService {

  API_URL = environment.apiUrl;
  DOMAIN_URL = environment.clientapiUrl;
  PROTOCOL = "http://"
  baseurl = this.API_URL;
  domainurl = this.DOMAIN_URL;
  protocol = this.PROTOCOL;
  // private readonly API_URL = 'assets/data/professors.json';
  dataChange: BehaviorSubject<Complaint[]> = new BehaviorSubject<Complaint[]>(
    []
  );
  // Temporarily stores data from dialogs
  dialogData: any;
  constructor(private httpClient: HttpClient) {}
  get data(): Complaint[] {
    return this.dataChange.value;
  }
  getDialogData() {
    return this.dialogData;
  }
  /** CRUD METHODS */
  getAllComplaint(): void {
    this.httpClient.get<Complaint[]>(this.baseurl + '/web/create/complaint/').subscribe(
      data => {
        this.dataChange.next(data);
      },
      (error: HttpErrorResponse) => {
        console.log(error.name + ' ' + error.message);
      }
    );
  }

  
  // DEMO ONLY, you can find working methods below
  addComplaint(complaint: Complaint): void {
    this.dialogData = complaint;
    this.createComplaint(complaint);
  }
  updateComplaint(complaint: Complaint): void {
    this.editComplaint(complaint);
    this.dialogData = complaint;
  }
  deleteComplaint(id: number): void {
    console.log(id);
    this.deleteComplaints(id);
  }

  createComplaint(
    complaint: Complaint
  ): Observable<Complaint> {
    const url = this.baseurl + '/web/create/complaint/';
    const formData = new FormData();
    formData.append('complain_type', complaint.complain_type);
    formData.append('complain_by', complaint.complain_by);
    formData.append('source', complaint.source);
    formData.append('phone_number', complaint.phone_number);
    formData.append('description', complaint.description);
    formData.append('action_taken', complaint.action_taken);
    formData.append('note', complaint.note);
    formData.append('status', complaint.status);
    // formData.append('attach_document', complaint.attach_document);
    return this.httpClient.request<Complaint>('POST', url, {body: formData});
  }

  editComplaint(
    complaint: Complaint
  ): Observable<Complaint> {
    const url = this.baseurl + '/web/edit/complaint/' + complaint.id + '/';
    const formData = new FormData();
    formData.append('complain_type', complaint.complain_type);
    formData.append('complain_by', complaint.complain_by);
    formData.append('source', complaint.source);
    formData.append('phone_number', complaint.phone_number);
    formData.append('description', complaint.description);
    formData.append('action_taken', complaint.action_taken);
    formData.append('note', complaint.note);
    formData.append('status', complaint.status);
    // formData.append('attach_document', complaint.attach_document);
    return this.httpClient.request<Complaint>('PUT', url, {body: formData});
  }

  deleteComplaints(
    id: any
  ): void {
    console.log("deleted");
    this.httpClient.delete<Complaint>(this.baseurl + '/web/edit/complaint/' + id + '/').subscribe(
      data => {
        console.log(data);
      },
      (error: HttpErrorResponse) => {
        console.log(error.name + ' ' + error.message);
      }
    );
  }

  
}
