import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Component, Inject, OnInit } from '@angular/core';
import { ProfessorsService, PhoneCallLog } from '../../../all-professors/professors.service';
// import { ProfessorsService, PhoneCallLog } from '../../professors.service';
import { ComplaintService } from '../../complaint.service';
import { Complaint } from '../../complaint';
import {
  FormControl,
  Validators,
  FormGroup,
  FormBuilder
} from '@angular/forms';
import { Professors } from '../../../all-professors/professors.model';
import { formatDate } from '@angular/common';

@Component({
  selector: 'app-complaint-form-dialog',
  templateUrl: './complaint-form-dialog.component.html',
  styleUrls: ['./complaint-form-dialog.component.sass']
})
export class ComplaintFormDialogComponent implements OnInit {

  action: string;
  dialogTitle: string;
  proForm: FormGroup;
  complaint: Complaint;

  constructor(
    public dialogRef: MatDialogRef<ComplaintFormDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public complaintService: ComplaintService,
    private fb: FormBuilder
  ) {
    // Set the defaults
    this.action = data.action;
    if (this.action === 'edit') {
      this.dialogTitle = data.complaint.name;
      this.complaint= data.complaint;
    } else {
      this.dialogTitle = 'New Phone Call Log';
      this.complaint = new Complaint({});
    }
    this.proForm = this.createContactForm();
  }
  formControl = new FormControl('', [
    Validators.required
    // Validators.email,
  ]);
  getErrorMessage() {
    return this.formControl.hasError('required')
      ? 'Required field'
      : this.formControl.hasError('email')
      ? 'Not a valid email'
      : '';
  }
  createContactForm(): FormGroup {
    return this.fb.group({

      id: [this.complaint.id],
      attach_document: [this.complaint.attach_document],
      note: [this.complaint.note],
      action_taken: [this.complaint.action_taken],
      date: [ this.complaint.date,
      ],
      // date: [ formatDate(this.complaint.date.date, 'yyyy-MM-dd', 'en'),
      //   [Validators.required]
      // ],
      source: [this.complaint.source],
      complain_type: [this.complaint.complain_type],
      complain_by: [this.complaint.complain_by],
      status: [this.complaint.status],
      phone_number: [this.complaint.phone_number],
      description: [this.complaint.description],
    });
  }

  onChange(event): void {
    if (event.target.files && event.target.files.length > 0) {
      this.complaint.attach_document = event.target.files[0];
    }
  }
  submit() {
    // emppty stuff
    
  }
  
  onNoClick(): void {
    this.dialogRef.close();
  }
  public confirmAdd(): void {
    this.complaintService.addComplaint(this.proForm.getRawValue());
    this.complaintService.createComplaint(
      this.proForm.value,
      // user_subdomain,
      // this.complaint.name,
      // this.complaint.call_duration,
      // this.complaint.description,
      // this.complaint.follow_up_date,
      // this.complaint.note,
      // this.complaint.call_type

    ).subscribe((res) => {
      // this.router.navigateByUrl('/create-client/' + `${res.data.id}`);
      console.log(res);
      // console.log(res.data.id);
      console.log("successfully logged in");
      // this.success = res;
      // this.success = "Your super admin account has been successfully created"
      
    }, (error) => {
      console.error(error);
      // this.error = error.error;
    });

   }

  // ngOnInit(): void {
  // }

  ngOnInit(): void {
  }

}
