import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { PhoneLogs } from './phone-logs';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable } from 'rxjs';
import { finalize, tap } from 'rxjs/operators';
import { environment } from '../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class PhoneLogsService {

  API_URL = environment.apiUrl;
  DOMAIN_URL = environment.clientapiUrl;
  PROTOCOL = "http://"
  baseurl = this.API_URL;
  domainurl = this.DOMAIN_URL;
  protocol = this.PROTOCOL;
  // private readonly API_URL = 'assets/data/professors.json';
  dataChange: BehaviorSubject<PhoneLogs[]> = new BehaviorSubject<PhoneLogs[]>(
    []
  );
  // Temporarily stores data from dialogs
  dialogData: any;
  constructor(private httpClient: HttpClient) {}
  get data(): PhoneLogs[] {
    return this.dataChange.value;
  }
  getDialogData() {
    return this.dialogData;
  }
  /** CRUD METHODS */
  getAllPhoneLogs(): void {
    this.httpClient.get<PhoneLogs[]>(this.baseurl + '/web/create/phone_call_log/').subscribe(
      data => {
        this.dataChange.next(data);
      },
      (error: HttpErrorResponse) => {
        console.log(error.name + ' ' + error.message);
      }
    );
  }

  
  // DEMO ONLY, you can find working methods below
  addPhoneLog(phoneLogs: PhoneLogs): void {
    this.dialogData = phoneLogs;
    this.createPhoneCallLog(phoneLogs);
  }
  updatePhoneCallLog(phoneLogs: PhoneLogs): void {
    this.editPhoneCallLog(phoneLogs);
    this.dialogData = phoneLogs;
    

  }
  deletePhoneLog(id: number): void {
    console.log(id);
    this.deletePhoneCallLog(id);
  }

  createPhoneCallLog(
    phoneLogs: PhoneLogs
  ): Observable<PhoneLogs> {
    const url = this.baseurl + '/web/create/phone_call_log/';
    const formData = new FormData();
    formData.append('name', phoneLogs.name);
    formData.append('call_duration', phoneLogs.call_duration);
    formData.append('description', phoneLogs.description);
    formData.append('follow_up_date', phoneLogs.follow_up_date);
    formData.append('note', phoneLogs.note);
    formData.append('phone_number', phoneLogs.phone_number);
    formData.append('call_type', phoneLogs.call_type);
    return this.httpClient.request<PhoneLogs>('POST', url, {body: formData});
  }

  editPhoneCallLog(
    phoneLogs: PhoneLogs
  ): Observable<PhoneLogs> {
    const url = this.baseurl + '/web/edit/phone_call_log/' + phoneLogs.id + '/';
    const formData = new FormData();
    formData.append('name', phoneLogs.name);
    formData.append('call_duration', phoneLogs.call_duration);
    formData.append('description', phoneLogs.description);
    // formData.append('follow_up_date', phoneLogs.follow_up_date);
    formData.append('note', phoneLogs.note);
    formData.append('phone_number', phoneLogs.phone_number);
    formData.append('call_type', phoneLogs.call_type);
    return this.httpClient.request<PhoneLogs>('PUT', url, {body: formData});
  }

  deletePhoneCallLog(
    id: any
  ): void {
    console.log("deleted");
    this.httpClient.delete<PhoneLogs>(this.baseurl + '/web/edit/phone_call_log/' + id + '/').subscribe(
      data => {
        console.log(data);
      },
      (error: HttpErrorResponse) => {
        console.log(error.name + ' ' + error.message);
      }
    );
  }

  
}
