import { formatDate } from '@angular/common';
export class Correspondence {
  id: any;
  from_title: string;
  to_title: any;
  reference_no: any;
  address: any;
  note: string;
  date: any;
  attach_document: any;
  call_type: string
    constructor(correspondence ) {
      this.id = correspondence.id || '';
      this.from_title = correspondence.from_title || '';
      this.to_title = correspondence.to_title || '';
      this.reference_no = correspondence.reference_no || '';
      this.address = correspondence.address || '';
      this.note = correspondence.note || '';
      this.date = formatDate(new Date(), 'yyyy-MM-dd', 'en') || '';
      this.attach_document = correspondence.attach_document || '';
      this.call_type = correspondence.call_type || ''
    }
}