export class SetUp {
  constructor(
      public id?: any,
      public template?: string,
      public logo?: any,
      public about_us?: string,
      public contact_us?: string,
      // public user_subdomain?: string,
      public header_content?: string,
      public footer_content?: string,
      public color?: any,
      public date?: any,
      public time?: any,
    ) {}
}

@Injectable({
  providedIn: 'root'
})
export class SetupService {
  API_URL = environment.apiUrl;
  DOMAIN_URL = environment.clientapiUrl;
  PROTOCOL = "http://"
  baseurl = this.API_URL;
  domainurl = this.DOMAIN_URL;
  protocol = this.PROTOCOL;

  constructor(
    private http: HttpClient
  ) { }

  createSetup(
    template: string,
    about_us: string,
    logo: any,
    contact_us: string,
    header_content: string,
    footer_content: string,
    color: string,
  ): Observable<SetUp> {
    const url = this.baseurl + '/web/create/setup/';
    const formData = new FormData();
    formData.append('template', template);
    formData.append('about_us', about_us);
    formData.append('logo', logo);
    formData.append('contact_us', contact_us);
    formData.append('header_content', header_content);
    formData.append('footer_content', footer_content);
    formData.append('color', color);
    return this.http.request<SetUp>('POST', url, {body: formData});
  }

  editSetup(
    id: any,
    template: string,
    about_us: string,
    logo: any,
    contact_us: string,
    header_content: string,
    footer_content: string,
    color: string,
  ): Observable<SetUp> {
    const url = this.baseurl + '/web/edit/setup/' + id + '/';
    const formData = new FormData();
    formData.append('template', template);
    formData.append('about_us', about_us);
    // formData.append('logo', logo);
    formData.append('contact_us', contact_us);
    formData.append('header_content', header_content);
    formData.append('footer_content', footer_content);
    formData.append('color', color);
    return this.http.request<SetUp>('PUT', url, {body: formData});
  }

  getSetup(): Observable<SetUp> {
    const url = this.baseurl + '/web/create/setup/';
    return this.http.request<SetUp>('GET', url);
  }
}
