import { formatDate } from '@angular/common';
export class Complaint {
  id: any;
  complain_type: string;
  complain_by: string;
  source: any;
  phone_number: any;
  date: any;
  description: string;
  action_taken: string;
  note: string;
  attach_document: any;
  status: string;  
  constructor(complaint) {
    this.id = complaint.id || '';
    this.complain_type = complaint.complain_type || '';
    this.complain_by = complaint.complain_by || '';
    this.source = complaint.source || '';
    this.phone_number = complaint.phone_number || '';
    this.note = complaint.note || '';
    this.date = formatDate(new Date(), 'yyyy-MM-dd', 'en') || '';
    this.attach_document = complaint.attach_document || '';
    this.description = complaint.description || ''
    this.status = complaint.status || '';
    this.action_taken = complaint.action_taken || ''
  }
}
