import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
// import { VisitorBook } from './visitor-book';
import {Enquiry} from './enquiry';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable } from 'rxjs';
import { finalize, tap } from 'rxjs/operators';
import { environment } from '../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class EnquiryService {

  API_URL = environment.apiUrl;
  DOMAIN_URL = environment.clientapiUrl;
  PROTOCOL = "http://"
  baseurl = this.API_URL;
  domainurl = this.DOMAIN_URL;
  protocol = this.PROTOCOL;
  // private readonly API_URL = 'assets/data/professors.json';
  dataChange: BehaviorSubject<Enquiry[]> = new BehaviorSubject<Enquiry[]>(
    []
  );
  // Temporarily stores data from dialogs
  dialogData: any;
  constructor(private httpClient: HttpClient) {}
  get data(): Enquiry[] {
    return this.dataChange.value;
  }
  getDialogData() {
    return this.dialogData;
  }
  /** CRUD METHODS */
  getAllEnquiry(): void {
    this.httpClient.get<Enquiry[]>(this.baseurl + '/web/create/admission_entry/').subscribe(
      data => {
        this.dataChange.next(data);
      },
      (error: HttpErrorResponse) => {
        console.log(error.name + ' ' + error.message);
      }
    );
  }

  
  // DEMO ONLY, you can find working methods below
  addEnquiry(enquiry: Enquiry): void {
    this.dialogData = enquiry;
    this.createAdmissionEnquiry(enquiry);
  }
  updateAdmissionEnquiry(enquiry: Enquiry): void {
    this.editAdmissionEnquiry(enquiry);
    this.dialogData = enquiry;
  }
  deleteEnquiry(id: number): void {
    console.log(id);
    this.deleteAdmissionEnquiry(id);
  }

  createAdmissionEnquiry(
    // name: string,
    // phone_number: string,
    // email: string,
    // address: string,
    // description: string,
    // note: string,
    // no_child_to_enroll: any,
    // reference: string,
    // follow_up_date: any,
    // status: string,
    // admin_note: string
    enquiry: Enquiry
  ): Observable<Enquiry> {
    const url = this.baseurl + '/web/create/admission_entry/';
    const formData = new FormData();
    formData.append('name', enquiry.name);
    formData.append('phone_number', enquiry.phone_number);
    formData.append('email', enquiry.email);
    formData.append('address', enquiry.address);
    formData.append('description', enquiry.description);
    formData.append('note', enquiry.note);
    formData.append('no_child_to_enroll', enquiry.no_child_to_enroll);
    formData.append('reference', enquiry.reference);
    formData.append('follow_up_date', enquiry.follow_up_date);
    formData.append('status', enquiry.status);
    formData.append('admin_note', enquiry.admin_note);
    return this.httpClient.request<Enquiry>('POST', url, {body: formData});
  }

  editAdmissionEnquiry( 
    enquiry: Enquiry
  ): Observable<Enquiry> {
    const url = this.baseurl + '/web/edit/admission_entry/'  + enquiry.id + '/';
    const formData = new FormData();
    formData.append('name', enquiry.name);
    formData.append('phone_number', enquiry.phone_number);
    formData.append('email', enquiry.email);
    formData.append('address', enquiry.address);
    formData.append('description', enquiry.description);
    formData.append('note', enquiry.note);
    formData.append('no_child_to_enroll', enquiry.no_child_to_enroll);
    formData.append('reference', enquiry.reference);
    formData.append('follow_up_date', enquiry.follow_up_date);
    formData.append('status', enquiry.status);
    formData.append('admin_note', enquiry.admin_note);
    return this.httpClient.request<Enquiry>('PUT', url, {body: formData});
  }

  deleteAdmissionEnquiry(
    id: any
  ): void {
    console.log("deleted");
    this.httpClient.delete<Enquiry>(this.baseurl + '/web/edit/admission_entry/' + id + '/').subscribe(
      data => {
        console.log(data);
      },
      (error: HttpErrorResponse) => {
        console.log(error.name + ' ' + error.message);
      }
    );
  }




 
}
