import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Component, Inject } from '@angular/core';
// import { ProfessorsService } from '../../../all-professors/professors.service';
import { VisitorsService } from '../../visitors.service';
import { VisitorBook } from '../../visitor-book';
@Component({
  selector: 'app-visitor-delete',
  templateUrl: './visitor-delete.component.html',
  styleUrls: ['./visitor-delete.component.sass']
})
export class VisitorDeleteComponent {

  constructor(
    public dialogRef: MatDialogRef<VisitorDeleteComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public visitorsService: VisitorsService
  ) {}
  onNoClick(): void {
    this.dialogRef.close();
  }
  confirmDelete(): void {
    this.visitorsService.deleteVisitor(this.data.id);
  }

}
