import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Component, Inject, OnInit } from '@angular/core';
import { ProfessorsService, PhoneCallLog } from '../../../all-professors/professors.service';
// import { ProfessorsService, PhoneCallLog } from '../../professors.service';
import { PhoneLogsService } from '../../phone-logs.service';
import { PhoneLogs } from '../../phone-logs';
import {
  FormControl,
  Validators,
  FormGroup,
  FormBuilder
} from '@angular/forms';
import { Professors } from '../../../all-professors/professors.model';
import {DateAdapter, MAT_DATE_FORMATS} from '@angular/material/core';
import { AppDateAdapter, APP_DATE_FORMATS } from '../../../shared/format-datepicker';
import { formatDate } from '@angular/common';

@Component({
  selector: 'app-phone-logs-form-dialog',
  templateUrl: './phone-logs-form-dialog.component.html',
  styleUrls: ['./phone-logs-form-dialog.component.sass'],
  providers: [
    {provide: DateAdapter, useClass: AppDateAdapter},
    {provide: MAT_DATE_FORMATS, useValue: APP_DATE_FORMATS}
  ]
})
export class PhoneLogsFormDialogComponent implements OnInit {

  action: string;
  dialogTitle: string;
  proForm: FormGroup;
  phoneLogs: PhoneLogs;

  constructor(
    public dialogRef: MatDialogRef<PhoneLogsFormDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public phoneLogsService: PhoneLogsService,
    private fb: FormBuilder
  ) {
    // Set the defaults
    this.action = data.action;
    if (this.action === 'edit') {
      this.dialogTitle = data.phoneLogs.name;
      this.phoneLogs = data.phoneLogs;
    } else {
      this.dialogTitle = 'New Phone Call Log';
      this.phoneLogs = new PhoneLogs({});
    }
    this.proForm = this.createContactForm();
  }
  formControl = new FormControl('', [
    Validators.required
    // Validators.email,
  ]);
  getErrorMessage() {
    return this.formControl.hasError('required')
      ? 'Required field'
      : this.formControl.hasError('email')
      ? 'Not a valid email'
      : '';
  }
  createContactForm(): FormGroup {
    return this.fb.group({
      id: [this.phoneLogs.id],
      call_duration: [this.phoneLogs.call_duration],
      name: [this.phoneLogs.name],
      // follow_up_date: [this.phoneLogs.follow_up_date,
      //   [Validators.required]
      // ],
      follow_up_date: [formatDate(this.phoneLogs.follow_up_date, 'yyyy-MM-dd', 'en'),
        [Validators.required]
      ],
      phone_number: [this.phoneLogs.phone_number],
      call_type: [this.phoneLogs.call_type],
      description: [this.phoneLogs.description],
      note: [this.phoneLogs.note],
    });
  }
  submit() {
    // emppty stuff
    
  }
  
  onNoClick(): void {
    this.dialogRef.close();
  }
  public confirmAdd(): void {
    this.phoneLogsService.addPhoneLog(this.proForm.getRawValue());
    this.phoneLogsService.createPhoneCallLog(
      this.proForm.value,
      

    ).subscribe((res) => {
      
      console.log(res);
     
      console.log("successfully logged in");
      
      
    }, (error) => {
      console.error(error);
      // this.error = error.error;
    });

   }

  // ngOnInit(): void {
  // }

  ngOnInit(): void {
  }

}
